*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.image {
  width: 100%;
  height: auto;
  margin: 0;
}

@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/inter-regular.woff2") format("woff2"), url("../fonts/inter-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/inter-medium.woff2") format("woff2"), url("../fonts/inter-medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/inter-bold.woff2") format("woff2"), url("../fonts/inter-bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 900;
  src: url("../fonts/inter-black.woff2") format("woff2"), url("../fonts/inter-black.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/montserrat-light.woff2") format("woff2"), url("../fonts/montserrat-light.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-display: swap;
}
.button {
  display: none;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 19.36px;
  border: none;
}
@media (min-width: 900px) {
  .button {
    display: inline-block;
    width: 262px;
    min-height: 61px;
  }
}
.button--cta {
  color: #E4E5EA;
  background-color: #4077f3;
}
.button--cta:hover {
  background-color: rgba(64, 119, 243, 0.7);
  cursor: pointer;
}
.button--cta:active {
  background-color: rgba(64, 119, 243, 0.3);
}

.contacts {
  display: flex;
}
.contacts--mobile {
  flex-direction: column;
}

@media (min-width: 585px) {
  .header .contacts {
    margin-left: auto;
  }
}
@media (min-width: 1170px) {
  .header .contacts {
    margin-left: 136px;
  }
}

.footer .contacts {
  margin-bottom: 10px;
}

.contacts__phone {
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19.36px;
}
.contacts__phone--mobile {
  margin-bottom: 8px;
}
@media (min-width: 770px) {
  .contacts__phone--mobile {
    margin-bottom: 21px;
  }
}
.contacts__phone--desktop {
  display: none;
}
@media (min-width: 620px) {
  .contacts__phone--desktop {
    display: block;
    padding-top: 15px;
  }
}

.contacts__social {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}
.contacts__social--mobile {
  gap: 10px;
}
@media (min-width: 770px) {
  .contacts__social--mobile {
    justify-content: flex-end;
    margin-bottom: 8px;
  }
}
.contacts__social--desktop {
  display: none;
}
@media (min-width: 770px) {
  .contacts__social--desktop {
    display: flex;
    gap: 30px;
    margin-left: 43px;
    padding-top: 17px;
  }
}

.contacts__social-item {
  display: flex;
  justify-content: center;
  align-items: center;
}
.contacts__social-item--mobile {
  width: 24px;
  height: 24px;
}
.contacts__social-item--desktop {
  width: 20px;
  height: 20px;
}
@media (min-width: 770px) {
  .contacts__social-item {
    width: 20px;
    height: 20px;
  }
}
.contacts__social-item:hover a svg {
  opacity: 70%;
}
.contacts__social-item:active a svg {
  opacity: 30%;
}

.page .container {
  max-width: 1140px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}
@media (min-width: 1170px) {
  .page .container {
    padding-left: 0;
    padding-right: 0;
  }
}

.decoration {
  display: none;
}
@media (min-width: 1000px) {
  .decoration {
    display: block;
  }
}

.decoration__red-light-ball {
  position: relative;
}
.decoration__red-light-ball::before {
  position: absolute;
  top: 251px;
  left: -102px;
  width: 311px;
  height: 311px;
  background-color: #961A1A;
  opacity: 50%;
  filter: blur(150px);
  border-radius: 50%;
  content: "";
}

.decoration__purple-light-ball {
  position: relative;
}
.decoration__purple-light-ball::before {
  position: absolute;
  top: -154px;
  left: 602px;
  width: 259px;
  height: 259px;
  background-color: #833AB4;
  opacity: 50%;
  filter: blur(150px);
  border-radius: 50%;
  content: "";
}

.decoration__red-big-ball {
  position: relative;
}
.decoration__red-big-ball::before {
  position: absolute;
  top: 25px;
  left: 505px;
  width: 100px;
  height: 100px;
  background-image: url("../img/red-big-ball.svg");
  background-repeat: no-repeat;
  content: "";
}

.decoration__purple-ball {
  position: relative;
}
.decoration__purple-ball::before {
  position: absolute;
  top: -55px;
  left: 855px;
  width: 80px;
  height: 80px;
  background-image: url("../img/purple-ball.svg");
  background-repeat: no-repeat;
  content: "";
}

.decoration__red-small-ball {
  position: relative;
}
.decoration__red-small-ball::before {
  position: absolute;
  top: 489px;
  left: 837px;
  width: 50px;
  height: 50px;
  background-image: url("../img/red-ball.svg");
  background-repeat: no-repeat;
  content: "";
}

.link {
  color: #E4E5EA;
  text-decoration: none;
}
.link:hover {
  opacity: 70%;
}
.link:active {
  opacity: 30%;
}

.menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  gap: 16px;
  overflow: scroll;
  padding: 18px 0 19px 0;
  text-transform: uppercase;
}
@media (min-width: 1140px) {
  .menu {
    text-transform: none;
    overflow: visible;
  }
}

@media (min-width: 940px) {
  .header .menu {
    gap: 30px;
    margin-left: 27px;
    padding-top: 15px;
  }
}

.menu__link {
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14.52px;
  letter-spacing: 0.15px;
}
@media (min-width: 1140px) {
  .menu__link {
    font-size: 16px;
    line-height: 19.36px;
  }
}

@media (min-width: 620px) {
  .page__body .header {
    display: flex;
  }
}
@media (min-width: 1140px) {
  .page__body .header {
    margin-top: 46px;
    margin-bottom: 94px;
  }
}

.main .headline-group {
  margin-bottom: 45px;
}

.headline-group__headline {
  margin-top: 0;
  margin-bottom: 16px;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 43.57px;
}
@media (min-width: 1140px) {
  .headline-group__headline {
    margin-bottom: 30px;
    font-size: 48px;
    line-height: 58.09px;
  }
}

.headline-group__highlighted {
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 900;
}

.headline-group__subheadline {
  margin-top: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 21.94px;
}

.highlight {
  color: transparent;
  background: linear-gradient(to right, #FCB045, #FF3F78);
  background-clip: text;
}

.logo {
  display: none;
}
@media (min-width: 1000px) {
  .logo {
    display: flex;
    flex-direction: column;
    width: 165px;
  }
}

.header .logo {
  margin-top: 11px;
}

.logo__image {
  margin-bottom: 10px;
}

.logo__descriptor {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 300;
  font-size: 10px;
  line-height: 12.19px;
}

.main {
  padding-top: 41px;
  padding-bottom: 58px;
}
@media (max-width: 900px) {
  .main {
    background-image: url("../img/background.png");
    background-repeat: no-repeat;
    background-size: cover;
  }
}
@media (min-width: 900px) {
  .main {
    display: flex;
  }
}
@media (min-width: 1170px) {
  .main {
    padding-top: 0;
  }
}

@media (min-width: 900px) {
  .offer {
    text-align: right;
  }
}

@media (min-width: 900px) {
  .main .offer {
    margin-left: auto;
  }
}

.offer__headline {
  margin-bottom: 21px;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 21.78px;
}
@media (min-width: 900px) {
  .offer__headline {
    margin-bottom: 41px;
  }
}

.offer__highlighted {
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 700;
}
@media (min-width: 900px) {
  .offer__highlighted {
    letter-spacing: 1px;
    text-transform: uppercase;
  }
}

.offer__text {
  margin-left: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 13px;
  line-height: 15.85px;
}
@media (min-width: 900px) {
  .offer__text {
    font-size: 16px;
    line-height: 19.5px;
  }
}

.offer__details--mobile .offer__text {
  position: relative;
  padding-left: 15px;
  letter-spacing: 0.7px;
}
.offer__details--mobile .offer__text::before {
  position: absolute;
  top: 6px;
  left: 0;
  width: 10px;
  height: 1px;
  background: linear-gradient(to right, #FCB045, #FF3F78);
  content: "";
}

.offer__point {
  margin-bottom: 6px;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 21.78px;
  letter-spacing: 0.8px;
  text-transform: uppercase;
}

.offer__details--mobile {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 125px 1fr;
  row-gap: 14px;
  text-transform: uppercase;
}
@media (min-width: 900px) {
  .offer__details--mobile {
    display: none;
  }
}
.offer__details--desktop {
  display: none;
}
@media (min-width: 900px) {
  .offer__details--desktop {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    max-width: 330px;
    margin-bottom: 63px;
  }
}

.page__body {
  margin: 0;
  color: #E4E5EA;
  background-color: #0E1014;
}

.footer {
  padding-top: 32px;
  padding-bottom: 60px;
}
@media (min-width: 752px) {
  .footer {
    display: flex;
    flex-direction: column;
  }
}
@media (min-width: 1140px) {
  .footer {
    padding-top: 55px;
    padding-bottom: 20px;
  }
}

.footer__navigation {
  display: flex;
  flex-wrap: wrap;
  gap: 19px;
  margin-bottom: 36px;
}
@media (min-width: 1140px) {
  .footer__navigation {
    gap: 98px;
  }
}

@media (min-width: 752px) {
  .footer__contacts {
    margin-left: auto;
    text-align: end;
  }
}

.footer__headline {
  margin-bottom: 20px;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 14.52px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #656566;
}
@media (min-width: 1140px) {
  .footer__headline {
    font-size: 14px;
    line-height: 16.94px;
  }
}

.footer__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.footer__list--extended {
  display: grid;
  grid-template-columns: 125px 123px;
  gap: 9px 23px;
}
@media (min-width: 1140px) {
  .footer__list--extended {
    grid-template-columns: 144px 212px;
    column-gap: 52px;
  }
}

.footer__text {
  margin-top: 0;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16.94px;
}
@media (min-width: 1140px) {
  .footer__text {
    font-size: 16px;
    line-height: 19.36px;
  }
}

@media (min-width: 752px) {
  .footer__legal-info {
    align-self: flex-end;
  }
}

.footer__legal {
  display: block;
  margin-bottom: 5px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 14.63px;
}
@media (min-width: 752px) {
  .footer__legal {
    text-align: end;
  }
}

.footer__legal.link {
  text-decoration: underline;
}